.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.react-select--custom.css-b62m3t-container .css-13cymwt-control {
  border-radius: 20px;
  border: 1px solid #c8c6c6;
}

.react-select--custom.css-b62m3t-container .css-t3ipsp-control {
  border-radius: 20px;
}

.react-select--custom.css-b62m3t-container .css-1xc3v61-indicatorContainer {
  color: #29293a;
}

.react-select--custom.css-b62m3t-container .css-1dimb5e-singleValue {
  font-weight: 500;
  margin-left: 5px;
}

.react-select--custom.css-b62m3t-container .css-1nmdiq5-menu {
  font-weight: 500;
}

.react-select--custom.css-b62m3t-container .css-1u9des2-indicatorSeparator {
  display: none;
}

.input-group {
  position: relative;
}

.input-group .error-message {
  font-size: 13px;
  color: red;
  font-weight: 400;
  position: absolute;
  bottom: -22px;
}

.react-select--custom.error.css-b62m3t-container .css-13cymwt-control {
  border: 1px solid red;
}

.react-datePicker--custom {
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  border-radius: 20px;
  border: 1px solid #c8c6c6;
  padding-left: 13px;
  height: 38px;
}

.react-datePicker--custom:focus {
  box-shadow: 0 0 0 1px #2864ff;
  border-color: #2864ff !important;
}

.react-datePicker--custom.error {
  border-color: red;
}

.redio_group {
  display: flex;
  align-items: flex-start;
}
.redio_group input {
  width: 16px;
  height: 16px;
  margin-right: 8px;
  flex-shrink: 0;
  margin-top: 6px;
}
.redio_group .label_text {
  color: #000000;
  font-weight: 500;
}

.redio_group_container + .redio_group_container {
  margin-top: 15px;
}

.text_input {
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  border-radius: 20px;
  border: 1px solid #c8c6c6;
  padding-left: 13px;
  height: 38px;
}

.text_input:focus {
  box-shadow: 0 0 0 1px #2864ff;
  border-color: #2864ff !important;
}

.text_input.error {
  border-color: red;
}

.checkbox_group input {
  width: 16px;
  height: 16px;
  margin-right: 8px;
  flex-shrink: 0;
}

.checkbox_group {
  display: flex;
  align-items: center;
}

.react-timePicker--custom input {
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  border-radius: 20px;
  border: 1px solid #c8c6c6;
  padding-left: 13px;
  height: 38px;
  color: #000000;
}

.react-timePicker--custom input:focus {
  box-shadow: 0 0 0 1px #2864ff;
  border-color: #2864ff !important;
}

.react-timePicker--custom.error input {
  border-color: red;
}

.react-timePicker--custom {
  width: 100%;
}

.phoneInput--custom input{
  border: unset;
}
.phoneInput--custom {
  height: 38px;
  border-radius: 20px;
  border: 1px solid #c8c6c6;
  padding-left: 13px;
  padding-right: 13px;
}

.phoneInput--custom:has(input:focus) {
  box-shadow: 0 0 0 1px #2864ff;
  border-color: #2864ff !important;
}

.phoneInput--custom.error {
  border-color: red;
}

.checkbox_group .label_text {
  font-size: 16px;
  font-weight: 500;
  color: #000000;
}

.checkbox_group .label_text span {
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
}

.checkbox_group .label_text span:hover {
  color: #7b1e27;
  text-decoration-color: #7b1e27;
}

.terms-and-conditions {
  padding: 16px;
  max-height: 200px;
  overflow-y: scroll;
  border-radius: 10px;
  border: 1px solid #dee2e6;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
}

.terms-and-conditions .red-text {
  font-weight: 700;
  color: #9c0b0b;
}

.terms-and-conditions .black-text {
  font-weight: bold;
  text-transform: uppercase;
  color: #000000;
  font-size: 20px;
}

.terms-and-conditions .red-text.large-size {
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  color: #9c0b0b;
  line-height: 24px;
}

.terms-and-conditions .red-text-thin {
  color: #9c0b0b;
}

.terms-and-conditions-item-2col {
  display: flex;
  align-items: flex-start;
  margin-bottom: 25px;
}

.terms-and-conditions-item-2col .number {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: #d9d9d9;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  flex-shrink: 0;
}

.terms-and-conditions-item-2col .text {
  margin-top: 5px;
}

.ReactModal__Body--open {
  overflow: hidden;
}

.ReactModal__Overlay {
  background-color: rgba(0, 0, 0, 0.2) !important;
  opacity: 1;
}

.react-modal--custom .ReactModal__Content {
  padding: 0 !important;
}

.form_input--label {
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: bold;
  color: #000000;
  display: flex;
  align-items: center;
}

.form_input--label .form_input--required {
  color: #dc3545;
  font-size: 20px;
  font-weight: bold;
  margin-left: 5px;
  line-height: 1;
}

.react-select--custom {
  width: 100%;
}

.form_input--description {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  margin-bottom: 8px;
}

.form_input--uploadFile {
  width: 100%;
  height: 200px;
  background: #fafafa;
  cursor: pointer;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: relative;
}

.form_input--uploadFile input {
  display: none;
}