* Main layout
* ==================================================
*/
.page {
	position: relative;
	overflow: hidden;
	min-height: 100vh !important;
}

.page-header {
	position: relative;
	padding: 0;
	margin: 0;
	border-bottom: none;
}

.frame-header {
	width: 100%;
	height: 500px;
	outline: none;
	border: 0;
}

