		height: 650px;
	}
}

@media (min-width: 1600px) {
	.frame-header {
		height: 730px;
	}
}

.fullwidth-page {
	position: relative;
	min-height: calc( 100vh - 56px);
	display: flex;
	align-items: stretch;
}

.fullwidth-page-inner {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-end;
	width: 100%;
}

.fullwidth-page-inner > * {
	position: relative;
	width: 100%;
}

.fullwidth-page:before {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	content: "";
	display: inline-block;
	background-color: rgba(0, 0, 0, 0.4);
}

@media (min-width: 992px) {
	.desktop .fullwidth-page {
		min-height: 100vh;
