}

.blick-overlay,
.blick-content {
	position: absolute;
}

.blick-overlay {
	top: 0;
	right: 0;
	left: 0;
	bottom: 32.16%;
	background-repeat: no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
}

.blick-overlay[data-blick-overlay='ipad'] {
	background-image: url(../public/images/blick-01.png);
}

.blick-content {
	position: absolute;
	overflow: hidden;
	top: 4.3%;
	bottom: 39.5%;
	left: 4.1%;
	right: 4.1%;
}

.blick-content img {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	object-fit: cover;
	height: 100%;
	width: 100%;
}

.blick-content[data-blick-content='ipad'] {
	top: 5.1%;
	bottom: 5.1%;
	left: 6.9%;
	right: 6.95%;
}

.blick-content[data-blick-content='iphone'] {
